html {
  font-size: 16px;
  color: #000; }

body {
  font-family: 'Montserrat', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto; }

.wrap {
  min-width: 320px;
  max-width: 1440px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto; }

.header {
  display: flex;
  justify-content: center;
  padding: 0.5rem;
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 600;
  background-color: #e6e6e6;
  max-width: 100%; }

.header__icon {
  display: flex;
  margin-left: 0.6rem; }

.title {
  display: flex;
  padding-left: 0;
  list-style: none;
  margin-top: 25px; }
  .title a {
    text-decoration: none;
    color: #4868BB;
    background-color: #e6e6e6;
    margin-left: 6rem; }
    .title a:hover {
      color: red; }

li {
  position: relative; }
  li:hover > .sub__title {
    display: flex;
    clip: auto;
    width: auto;
    height: auto;
    margin: 0; }

.sub__title {
  flex-direction: column;
  position: absolute;
  white-space: nowrap;
  margin-top: 1rem;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }
  .sub__title a {
    padding: 1rem;
    margin: 0;
    width: 89%;
    font-weight: normal; }

.menu__arrow {
  display: inline-block;
  width: 0;
  height: 0;
  border-top: 4px solid red;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent; }

.navigation {
  display: none; }
  .navigation .checkbox {
    margin: 0.6rem 0 0 0.3rem; }
  .navigation span {
    display: block;
    background: #566973;
    border-radius: 2px; }
  .navigation a {
    text-decoration: none;
    color: #4868BB;
    transition: color 0.3s ease; }
  .navigation a:hover {
    color: red; }
  .navigation #menuToggle {
    display: block;
    position: fixed;
    top: 30px;
    right: 27px;
    padding: 7px 4px 4px 4px;
    background: #e6e6e6;
    z-index: 1;
    -webkit-user-select: none;
    user-select: none; }
  .navigation #menuToggle input {
    display: block;
    width: 40px;
    height: 32px;
    position: absolute;
    top: -7px;
    left: -5px;
    cursor: pointer;
    opacity: 0;
    /* hide this */
    z-index: 2;
    /* and place it over the hamburger */
    -webkit-touch-callout: none; }
  .navigation #menuToggle span {
    display: flex;
    width: 33px;
    height: 4px;
    margin-bottom: 5px;
    position: relative;
    background: #4868BB;
    border-radius: 3px;
    z-index: 1;
    transform-origin: 4px 0;
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease; }
  .navigation #menuToggle span:first-child {
    transform-origin: 0 0; }
  .navigation #menuToggle span:nth-last-child(2) {
    transform-origin: 0 100%; }
  .navigation #menuToggle input:checked ~ span {
    opacity: 1;
    transform: rotate(45deg) translate(-2px, -1px);
    background: #4868BB; }
  .navigation #menuToggle input:checked ~ span:nth-last-child(3) {
    opacity: 0;
    transform: rotate(0deg) scale(0.2, 0.2); }
  .navigation #menuToggle input:checked ~ span:nth-last-child(2) {
    opacity: 1;
    transform: rotate(-45deg) translate(0, -1px); }
  .navigation #menu {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    margin: 0;
    padding: 75px 0 0 0;
    background: #e6e6e6;
    text-align: center;
    list-style-type: none;
    -webkit-font-smoothing: antialiased;
    /* to stop flickering of text in safari */
    transform-origin: 0 0;
    transform: translate(100%, 0);
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1); }
  .navigation #menu li {
    padding: 10px 0;
    font-size: 22px; }
  .navigation #menuToggle input:checked ~ ul {
    transform: none;
    opacity: 1; }
  .navigation .sub__title2 {
    font-size: 1rem;
    line-height: 1.5;
    font-weight: 400; }

